
.slides{
	display:flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
  font-family: 'Dancing Script', cursive;}

h1{
    font-family: 'Calligraffitti', cursive;
    font-size: 3em;
    text-decoration: underline;
}
h2{
  font-size: 3em;
}
p{
  margin: 0.5em;
  font-size: 1.8em;
}


.containerSlide {
  width: 40em;
  margin: auto;
  border: solid black;
  border-radius: 1em;
  margin-bottom: 1em;
}
.active {
  margin: auto;
}
.each-slide {
  width: 25em;
  margin: auto;
}
.containerSlide img {
  width: 25em;
}
